body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.hero {
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero:after {
  content: " ";
  display: table;
  clear: both;
}

.hero__title {
  font-size: 56px;
  font-family: roboto-light, Arial, sans-serif;
  font-weight: 100;
  color: #333333;
  line-height: 1.2;
  text-align: center;
}

.hero__text {
  display: block;
  font-size: 15px;
  font-family: roboto-medium, Arial, sans-serif;
  color: #333333;
  line-height: 1.2;
  text-align: center;
  margin: 20px 0;
}

.hero__center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 900px;
  width: 100%;
  text-align: center;
}

.hero__center:after {
  content: " ";
  display: table;
  clear: both;
}

.hero__social {
  font-size: 1rem;
  position: absolute;
  right: 0;
  top: 50%;
}

.social__icon {
  float: right;
  width: 100%;
  color: #bababa;
  cursor: pointer;
}

.button__download {
  height: 52px;
  width: 161px;
  border-radius: 25px;
  border: 5px solid #77cabe;
  font-size: 16px;
  font-family: roboto-bold, Arial, sans-serif;
  color: #444444;
  font-weight: bold;
  line-height: 1.2;
}

.button__download:hover {
  box-shadow: 7px 7px 5px 0px rgba(50, 50, 50, 0.49);
}

.font_bold {
  font-family: roboto-bold, Arial, sans-serif;
  font-weight: 700;
}

.container {
  position: relative;
  height: 100vh;
  margin: 0 auto;
  min-height: 500px;
  max-width: 1400px;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

.header {
  position: relative;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.logo {
  font-size: 27px;
  font-family: roboto-light, Arial, sans-serif;
  color: #333333;
  line-height: 1.2;
  text-align: left;
  float: left;
  margin: 30px;
  display: inline-block;
}

.logo__link {
  position: relative;
  display: inline-block;
}

.logo__img {
  position: absolute;
  top: -27px;
  left: 50%;
  transform: translateX(-50%);
}

.nav {
  float: right;
  display: inline-block;
}

.nav ul li {
  display: inline-block;
  font-size: 15px;
  font-family: roboto-medium, Arial, sans-serif;
  color: #333333;
  line-height: 1.2;
  text-align: right;
}

.nav ul li a {
  text-decoration: none;
  cursor: pointer;
}

.nav ul li a:hover {
  border-bottom: 3px solid #22ff88;
}

.basket, .menu {
  float: right;
  display: inline-block;
  margin-left: 30px;
}

.basket:after, .menu:after {
  content: " ";
  display: table;
  clear: both;
}

.basket {
  position: relative;
}

.basket .quantity {
  position: absolute;
  top: 15px;
  left: 20px;
  height: 25px;
  width: 25px;
  padding: 6px;
  background-color: #7fbc00;
  border-radius: 100%;
  color: #ffffff;
  border: 2px solid #ffffff;
  font-size: 12px;
  font-family: roboto-medium, Arial, sans-serif;
  line-height: 1.1;
}

.basket .total {
  display: inline-block;
  font-size: 15px;
  font-family: roboto-medium, Arial, sans-serif;
  color: #333333;
  line-height: 1.2;
  text-align: right;
}

.naturix {
  font-size: 15px;
  font-family: roboto-medium, Arial, sans-serif;
  color: #888888;
  line-height: 1.2;
  text-align: left;
  font-weight: bold;
  position: absolute;
  bottom: 50px;
  left: 10%;
}

.naturix__organic {
  font-family: roboto-italic, Arial, sans-serif;
  font-style: italic;
  color: #79b100;
}

.style {
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.style img {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
}

.organic {
  max-width: 1800px;
  margin: 0 auto;
}

.organic:after {
  content: " ";
  display: table;
  clear: both;
}

.organic__menu {
  margin: 0 auto;
  text-align: center;
  padding: 0;
  width: 100%;
}

.organic__menu:after {
  content: " ";
  display: table;
  clear: both;
}

.organic__menu ul {
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  text-align: center;
  max-width: 1170px;
}

.organic__menu ul li {
  float: left;
  display: inline-block;
  margin: 0 1.1%;
  cursor: pointer;
  box-sizing: border-box;
}

.organic__menu ul li a {
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  font-family: roboto-medium, Arial, sans-serif;
  color: #9f9e9c;
  border-radius: 25px;
  height: 47px;
  width: 180px;
  border: 2px solid #9f9e9c;
  padding: 10px 0;
  box-sizing: border-box;
}

.organic__menu ul li a:hover {
  background-color: #81ba00;
  color: #ffffff;
  box-shadow: 5px 5px 15px -6px #000000;
}

.banner__allnature {
  float: left;
  display: inline-block;
  margin: 0 2.5% 0 2.9%;
}

.banner__allnature:after {
  content: " ";
  display: table;
  clear: both;
}

.products {
  margin: 0 auto;
  margin-top: 30px;
  padding: 0;
  text-align: center;
  display: inline-block;
  width: 100%;
}

.products:after {
  content: " ";
  display: table;
  clear: both;
}

.product {
  box-sizing: border-box;
  width: 15%;
  margin-right: 0.83333333333333333333333333333333%;
  margin-left: 0.83333333333333333333333333333333%;
  float: left;
  display: inline-block;
}

.product:after {
  content: " ";
  display: table;
  clear: both;
}

.product__img {
  background-color: #f7f6f2;
  position: relative;
  width: 100%;
  height: 350px;
  border-radius: 18px;
}

.product__img img {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  margin: 0 auto;
  opacity: 1;
}

.product__text {
  font-size: 20px;
  margin: 25px 0;
  font-family: roboto-medium, Arial, sans-serif;
  color: #525252;
  line-height: 1.2;
  text-align: center;
}

.product__price {
  font-size: 14px;
  font-family: helvetica, Arial, sans-serif;
  color: #7fbb00;
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
}

.product__likes {
  font-size: 1rem;
}

.product__likes a {
  color: #dedede;
}

.marker {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  display: block;
}

.marker__new, .marker__sale {
  height: 25px;
  width: 70px;
  border-radius: 13px;
  font-size: 11px;
  display: inline-block;
  font-family: helvetica, Arial, sans-serif;
  color: #ffffff;
  font-weight: bold;
  line-height: 1.2;
  text-align: left;
  text-transform: uppercase;
  text-align: center;
  padding: 3px;
  border: 1px solid #ffffff;
}

.marker__new {
  background-color: #92d2a7;
}

.marker__sale {
  background-color: #79d1d6;
}

.wrapper {
  margin: 0 auto;
  text-align: center;
  max-width: 250px;
}

.pages {
  margin: 30px auto;
  padding: 30px 0;
  text-align: center;
  display: block;
}

.pages .page {
  height: 5px;
  width: 50px;
  background-color: #f1f1f1;
  margin: 0;
  padding: 0;
  float: left;
  display: inline-block;
  border-right: 2px solid #ffffff;
}

.pages .active {
  background-color: #7fbb00;
}
